.shakeAnimation {
  animation-name: shake;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
}

@keyframes shake {
  0% { transform: translate(-2px, 0px) }
  2% { transform: translate(0px, 0px) }
  4% { transform: translate(-2px, 0px) }
  6% { transform: translate(0px, 0px) }
  8% { transform: translate(-2px, 0px) }
  10% { transform: translate(0px, 0px) }
  12% { transform: translate(-2px, 0px) }
  14% { transform: translate(0px, 0px) }
  16% { transform: translate(-2px, 0px) }
  18% { transform: translate(0px, 0px) }
  20% { transform: translate(-2px, 0px) }
  22% { transform: translate(0px, 0px) }
  100% { transform: translate(0px, 0px) }
}

.changeColor {
  animation-name: changeCol;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes changeCol {
  0% { color: red; transform: scale(1.0) }
  24.99% { color: red; transform: scale(1.0) }
  25% { color: green; transform: scale(1.1)}
  49.99% { color: green; transform: scale(1.1)}
  50% { color: darkviolet; transform: scale(1.0) }
  74.99% { color: darkviolet; transform: scale(1.0) }
  75% { color: blue; transform: scale(1.1) }
  100% { color: blue; transform: scale(1.1) }
}
    
